html { height: 100%; }

body { height: 100%; font: 14px/24px 'Roboto', sans-serif; background-color: #e8e8e8; }

.main-wrapper { display: flex; justify-content: flex-start; flex-direction: column; min-height: 100%; position: relative; overflow: hidden; }

.wrapper { width: 1240px; margin: 0 auto; padding: 0 20px; margin-top: 30px; position: relative; }

.header { background-color: #fb0; height: 64px; width: 100%; padding: 0 30px; display: flex; flex-direction: row; justify-content: space-between; order: 1; }

.header__head { margin: 0; font-weight: 400; font-size: 22px; }

.header-left { display: flex; flex-direction: row; align-items: center; justify-content: space-between; width: 120px; }

.header__menu-btn { font-size: 21px; cursor: pointer; width: 35px; height: 35px; border-radius: 50%; display: flex; flex-direction: row; justify-content: center; align-items: center; margin-top: -2px; opacity: .6; }

.header__menu-btn:hover { background: rgba(255, 255, 255, 0.5); opacity: 1; }

.header-right { display: flex; flex-direction: row; align-items: center; justify-content: space-between; }

.header-main-style { display: flex; flex-direction: row; align-items: center; justify-content: space-between; width: 110px; }

.header-main-style__item { font-size: 25px; cursor: pointer; opacity: .6; }

.header-main-style__item:hover { opacity: 1; }

.footer { min-height: 50px; order: 3; background-color: #fff; }

.main { order: 2; flex-grow: 1; display: inherit; }

.new-note, .note-popup { display: flex; flex-direction: column; max-width: 600px; margin: 0 auto; background-color: #fff; transition: .5s; padding: 10px; box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12); }

.new-note__head, .note-popup__head { display: none; padding: 4px; outline: none; border: none; font-weight: 600; color: #424242; background: transparent; margin-bottom: 10px; font-size: 16px; }

.new-note__text, .note-popup__text { resize: none; padding: 4px; outline: none; border: none; color: #424242; background: transparent; height: 26px; font-size: 16px; }

.new-note__bottom, .note-popup__bottom { display: none; flex-direction: row; justify-content: space-between; margin-top: 10px; }

.new-note__btn, .note-popup__btn { font-size: 14px; transition: .3s; font-weight: bold; color: #424242; border: none; background: none; outline: none; cursor: pointer; }

.new-note__btn:hover, .note-popup__btn:hover { background: #dbdbdb; transition: .3s; }

.new-note-color { position: relative; }

.new-note-color:hover .new-note-color__div, .new-note-color:hover .note-popup-color__div { opacity: 1; pointer-events: auto; transition: .5s; }

.new-note-color__btn, .note-popup-color__btn { color: #424242; padding: 0 4px; font-size: 16px; }

.new-note-color__div, .note-popup-color__div { display: flex; opacity: 0; pointer-events: none; transition: .5s; width: 103px; flex-direction: row; flex-wrap: wrap; justify-content: space-between; position: absolute; top: -110px; background: #fff; box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.4); padding: 5px; }

.new-note-color__div:after, .note-popup-color__div:after { content: ''; width: 100%; height: 20px; position: absolute; bottom: -20px; left: 0; }

.new-note-color__item, .note-popup-color__item { width: 25px; height: 25px; border-radius: 50%; margin: 3px; }

.new-note-color__item[data-note-color='#ffffff'], [data-note-color='#ffffff'].note-popup-color__item { background-color: #ffffff; border: 2px solid #e0e0e0; }

.new-note-color__item[data-note-color='#cecece'], [data-note-color='#cecece'].note-popup-color__item { background-color: #cecece; border: 2px solid #cecece; }

.new-note-color__item[data-note-color='#c4b6b6'], [data-note-color='#c4b6b6'].note-popup-color__item { background-color: #c4b6b6; border: 2px solid #c4b6b6; }

.new-note-color__item[data-note-color='#e59292'], [data-note-color='#e59292'].note-popup-color__item { background-color: #e59292; border: 2px solid #e59292; }

.new-note-color__item[data-note-color='#edb674'], [data-note-color='#edb674'].note-popup-color__item { background-color: #edb674; border: 2px solid #edb674; }

.new-note-color__item[data-note-color='#f2f07b'], [data-note-color='#f2f07b'].note-popup-color__item { background-color: #f2f07b; border: 2px solid #f2f07b; }

.new-note-color__item[data-note-color='#b2fca4'], [data-note-color='#b2fca4'].note-popup-color__item { background-color: #b2fca4; border: 2px solid #b2fca4; }

.new-note-color__item[data-note-color='#96cff7'], [data-note-color='#96cff7'].note-popup-color__item { background-color: #96cff7; border: 2px solid #96cff7; }

.new-note-color__item[data-note-color='#d7adff'], [data-note-color='#d7adff'].note-popup-color__item { background-color: #d7adff; border: 2px solid #d7adff; }

.new-note-color__item:hover, .note-popup-color__item:hover { border: 2px solid #9b9b9b; }

.note-list { display: flex; flex-direction: row; flex-wrap: wrap; position: relative; margin-top: 50px; }

.note-list__main-head, .favorite-note-list__head { font-weight: 500; font-size: 16px; margin: 0; position: absolute; top: -35px; color: #707070; display: none; }

.note-list__item { width: 225px; background-color: #ffffff; box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12); transition: .5s; padding: 15px; position: relative; margin-bottom: 20px; margin-right: 15px; min-height: 200px; }

.note-list__item:hover { box-shadow: 0px 0px 19px -5px rgba(0, 0, 0, 0.75); transition: .5s; }

.note-list__item:hover .note-list__del { opacity: 1; transition: .5s; }

.note-list__item:hover .note-list__pin { opacity: .6; transition: .5s; }

.note-list__item:hover .note-list__pin:hover { opacity: 1; transition: .5s; }

.note-list__head { font-size: 18px; margin: 0; }

.note-list__text { font-size: 16px; margin: 0; overflow-wrap: break-word; padding-bottom: 20px; }

.note-list__del { position: absolute; top: -12.5px; right: -12.5px; background-color: #fff; font-weight: bold; cursor: pointer; display: flex; flex-direction: row; justify-content: center; align-items: center; width: 25px; height: 25px; border-radius: 50%; box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.2); opacity: 0; transition: .5s; }

.note-list__del i { font-size: 18px; margin-bottom: -1px; }

.note-list__pin { position: absolute; bottom: 5px; right: 5px; cursor: pointer; transition: .5s; opacity: 0; }

.note-list__pin i { font-size: 18px; }

.favorite-note-list { display: flex; flex-direction: row; flex-wrap: wrap; position: relative; margin-top: 50px; }

.favorite-note-list__head { display: block; }

.favorite-note-list .note-list__item .note-list__pin { opacity: 1; transform: rotate(-45deg); color: #2752ff; }

.note-popup { width: 600px; position: absolute; top: 150%; left: 50%; transform: translateX(-50%); z-index: 10; display: none; }

.note-popup__head { display: block; }

.note-popup__bottom { display: flex; }

.note-popup-color { position: relative; }

.note-popup-color:hover .note-popup-color__div { opacity: 1; pointer-events: auto; transition: .5s; }

.note-popup-color__item[data-note-color='#ffffff'] { background-color: #ffffff; border: 2px solid #e0e0e0; }

.note-popup-color__item[data-note-color='#cecece'] { background-color: #cecece; border: 2px solid #cecece; }

.note-popup-color__item[data-note-color='#c4b6b6'] { background-color: #c4b6b6; border: 2px solid #c4b6b6; }

.note-popup-color__item[data-note-color='#e59292'] { background-color: #e59292; border: 2px solid #e59292; }

.note-popup-color__item[data-note-color='#edb674'] { background-color: #edb674; border: 2px solid #edb674; }

.note-popup-color__item[data-note-color='#f2f07b'] { background-color: #f2f07b; border: 2px solid #f2f07b; }

.note-popup-color__item[data-note-color='#b2fca4'] { background-color: #b2fca4; border: 2px solid #b2fca4; }

.note-popup-color__item[data-note-color='#96cff7'] { background-color: #96cff7; border: 2px solid #96cff7; }

.note-popup-color__item[data-note-color='#d7adff'] { background-color: #d7adff; border: 2px solid #d7adff; }

.note-popup-color__item:hover { border: 2px solid #9b9b9b; }

@keyframes overlayOpacity { from { opacity: 0; }
  to { opacity: 1; } }

.overlay { display: none; position: absolute; width: 100%; height: 100%; background-color: rgba(235, 235, 235, 0.6); z-index: 5; left: 0; right: 0; top: 0; bottom: 0; opacity: 0; transition: 1s; animation: overlayOpacity 0.5s linear; animation-fill-mode: forwards; }
